:root {
  --background-color: #1C1A24;
  --text-color: #f4f4f4;
  --border-color: #7850FF;
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: 'Roboto Mono', monospace;
}

.title {
  text-align: center;
  font-size: 26px;
  margin-bottom: 20px;
  font-family: 'ClumsyPixel', sans-serif;
}

.search-bar {
  text-align: center;
  margin-bottom: 10px;
}

.search-bar input {
  font-size: 18px;
  padding: 10px;
  width: 300px;
  border: 1px solid var(--border-color);
  border-radius: 5px;
  background-color: var(--text-color);
  color: var(--background-color);
}

.search-bar button {
  font-size: 18px;
  font-family: ClumsyPixel;
  font-weight: 500;
  padding: 10px 20px;
  background-color: #7850FF;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 10px;
}

.sections-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  border-left: 2px solid var(--border-color);
  border-right: 2px solid var(--border-color);
  border-top: 2px solid var(--border-color);
  border-bottom: 2px solid var(--border-color);
  margin-left: 100px;
  margin-right: 100px;
  box-sizing: border-box;
}

.section {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 20px;
  width: 33.33%;
  box-sizing: border-box;
  font-size: 16px;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

.middle-section {
  text-align: center;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 300px;
  border-left: 2px solid var(--border-color);
  border-right: 2px solid var(--border-color);
  width: 33.33%;
}

.section p {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: left;
  vertical-align: middle;
  padding-bottom: 6px;
  margin-bottom: 6px;
  padding-top: 6px;
  margin-top: 6px;
}

.section p:last-child {
  margin-bottom: 0;
}

.section p span {
  font-weight: normal;
  text-align: right;
  align-self: center;
  vertical-align: middle;
  justify-content: space-between;
}

.section img {
  width: 28px;
  height: 28px;
  margin-right: 10px;
  align-self: center;
  vertical-align: middle;
  justify-content: space-between;
}

.section p::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: -6px;
  height: 1px;
  background-color: var(--border-color);
}

.ghost-svg {
  width: 100%;
  height: auto;
  max-height: 100%;
  margin-top: 40px;
  margin-bottom: 100px;
}

@media screen and (max-width: 768px) {
  .sections-container {
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
  }

  .section,
  .middle-section,
  .left-section,
  .right-section {
    width: 100%;
    font-size: 16px;
  }

  .middle-section {
    border-left: none;
    border-right: none;
    border-top: 0px solid var(--border-color);
    border-bottom: 0px solid var(--border-color);
    order: 1;
  }

  .left-section {
    order: 2;
    border-top: 2px solid var(--border-color);
    border-bottom: 2px solid var(--border-color);
  }

  .right-section {
    order: 3;
  }

  .ghost-svg {
    width: 100%;
    height: auto;
    max-height: 100%;
    margin-top: 20px;
    margin-bottom: 10px;
  }
}